import { Suspense } from "react";
import { useLocation, useSearchParams } from "react-router-dom";
import QvipleLoader from "../../Loader/QvipleLoader";
import StudentFeedbackDetail from "../../InstituteMember/InstituteFeedback/StudentFeedback/StudentFeedbackDetail/StudentFeedbackDetail";
import StudentFeedbackChart from "../../InstituteMember/InstituteFeedback/StudentFeedback/StudentFeedbackChart";
import StudentFeedback from "../../InstituteMember/InstituteFeedback/StudentFeedback/StudentFeedback";
import UserMemberMainWrapper from "../../Layout/UserMemberTab/UserMemberMainWrapper";

const StudentFeedbackManager = () => {
  const getQuery = useLocation();
  const [searchParams] = useSearchParams();
  //   console.info("getQuery", getQuery.state);
  return (
    <>
      <UserMemberMainWrapper>
        <Suspense fallback={<QvipleLoader />}>
          {searchParams.get("a") === "feedback-detail" ? (
            <StudentFeedbackDetail
              instituteId={getQuery.state?.onClickValue?.institute?._id}
              isManager
            />
          ) : searchParams.get("a") === "feedback-analytic" ? (
            <StudentFeedbackChart
              instituteId={getQuery.state?.onClickValue?.institute?._id}
              ifid={getQuery.state?.ifid}
            />
          ) : (
            <StudentFeedback
              instituteId={getQuery.state?.onClickValue?.institute?._id}
              isManager
            />
          )}
        </Suspense>
      </UserMemberMainWrapper>
    </>
  );
};

export default StudentFeedbackManager;
